.background {
  background-image: url(../img/pemkot.jpg);
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginContainer {
  background-color: white;
  padding: 50px;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.kosong {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: none;
  background-color: lightgray;
}

.button {
  width: 100%;
  padding: 10px;
  background-color: #ad241b;
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  margin-top: 10px;
}

.input::placeholder {
  color: gray;
  font-style: italic;
}